@import url("https://fonts.googleapis.com/css2?family=Goldman:wght@400;700&family=Iceberg&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.btn:hover {
  cursor: pointer;
  background-color: rgb(108, 99, 255);
  box-shadow: 1px 1px 6px rgb(108, 99, 255);
  transition: 0.5s ease-out;
  color: white;
}

.navbar {
  width: 100vw;
  display: flex;
  justify-content: space-between;
  height: 10vh;
  position: sticky;
}
.bright .navbar {
  /* background-color: #005; */
  box-shadow: 1px 1px 10px rgb(108, 99, 255);
}
.dark .navbar {
  position: relative;
  z-index: 5;
  background-color: #222831;
  box-shadow: 1px 1px 6px white;
}
.navbar ul {
  display: flex;
  align-items: center;
  gap: 50px;
  margin-left: 15px;
}
.navbar ul li {
  list-style: none;
  font-weight: bold;
}
.dark .navbar ul li {
  color: rgb(108, 99, 255);
}
.navbar ul li:hover {
  cursor: pointer;
}
.navbar .searchbar {
  display: flex;
  align-items: center;
  gap: 25px;
  height: 100%;
}
.searchinput {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50%;
  border: 1px solid black;
  padding: 2px;
  border-radius: 8px;
  width: 18vw;
}
.dark .searchinput {
  background-color: white;
}
.searchinput img {
  height: 20px;
  width: 20px;
}
.navbar button {
  height: 50%;
  padding: 0 10px 0 10px;
  border-radius: 7px;
  border: 1px solid black;
  background-color: white;
}
.navbar input {
  height: 100%;
  border: none;
  outline: none;
  margin-left: 5px;
  width: 100%;
}
.modeIcon {
  display: flex;
  align-items: center;
}
.navbar .modeIcon img {
  height: 40px;
  width: 40px;
  margin-right: 25px;
}
.navbar .modeIcon img:hover {
  cursor: pointer;
}
.list {
  text-decoration: none;
  color: black;
}

@media (max-width: 480px) {
  .navbar {
    width: 100vw;
  }
  .navbar .searchbar {
    display: none;
  }
  .navbar .modeIcon img {
    width: 30px;
    height: 30px;
  }
  .navbar ul li {
    font-size: 15px;
  }
}

/* ********************************** Signup and Login Continer ********************************* */

.mainSignupContainer {
  height: calc(100vh - 10vh - 7.5vh);
  display: flex;
  width: 100vw;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.dark .mainSignupContainer {
  background-color: #31363f;
  color: white;
}

form p {
  text-align: center;
  color: red !important;
  font-size: smaller;
  font-weight: 500;
}

.director {
  font-family: Arial, Helvetica, sans-serif;
  font-size: normal;
  text-decoration: none;
}
.director p {
  color: rgb(108, 99, 255) !important;
  font-weight: 500;
}
.remainArea {
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.mainSignupContainer .heading {
  display: flex;
  align-items: center;
  justify-content: center;
}

.heading h1 {
  font-size: 60px;
  font-family: Iceberg;
  text-align: center;
}
.mainSignupContainer .heading h1 {
  position: fixed;
  top: 10%;
}
.heading h1 span {
  color: rgb(108, 99, 255);
}
.dark .heading h1 {
  color: white;
}
.signupContainer {
  padding: 10px;
  width: 30vw;
  /* background-color: #005; */
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgb(108, 99, 255);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: 50px;
  margin-top: 50px;
}
.signupContainer label {
  display: block;
  margin-top: 3px;
  margin-bottom: 2px;
  margin-left: px;
}
.signupContainer input {
  height: 5vh;
  width: 23vw;
  border-radius: 8px;
  padding-left: 5px;
  border: 1px solid black;
  outline: none;
}
.user {
  margin-bottom: -10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.signupContainer .user img {
  height: 60px;
  border: 1px solid black;
  border-radius: 50%;
}

.signupContainer .passClass img {
  height: 25px;
  width: 25px;
  margin-right: 8px;
}
.signupContainer .passClass img:hover {
  cursor: pointer;
}

.signupContainer .passClass {
  border-radius: 8px;
  width: 23svw;
  border: 1px solid black;
  display: flex;
  align-items: center;
  background-color: white;
}

.signupContainer .passClass input {
  width: 100%;
  border: none;
  outline: none;
}
#submit {
  width: 25%;
  border: none;
  margin-top: 15px;
}

.signupContainer p {
  margin-top: 5px;
}
.signupContainer p:hover {
  cursor: pointer;
}
.dark .signupContainer p {
  color: rgb(108, 99, 255);
}
.bright #submit {
  /* background-color: rgb(0, 119, 255); */
  background-color: rgb(108, 99, 255);
  color: white;
  box-shadow: 1px 1px 4px rgb(185, 185, 230);
}
.bright form .signupContainer p {
  color: rgb(108, 99, 255);
}

@media (max-width: 480px) {
  .mainSignupContainer {
    height: 73.5vh;
  }
  .remainArea {
    display: flex;
    /* flex-direction: column; */
  }
  .mainSignupContainer .heading h1 {
    position: relative;
    font-size: 55px;
    /* margin-top: -250px; */
    margin-bottom: 25px;
  }
  .mainSignupContainer {
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    /* align-items: center; */
  }
  .mainSignupContainer form {
    display: flex;
    align-self: flex-start !important;
  }
  .signupContainer {
    margin: 0;
    width: 80vw;
    font-size: smaller;
    display: flex;
    justify-content: center !important;
  }
  .signupContainer input,
  .username,
  .password,
  .email,
  .cnpass {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90%;
    margin-left: 0 !important;
  }

  .signupContainer .email label {
    margin-left: -65%;
    margin-bottom: 3px;
  }
  .signupContainer .username label {
    margin-left: -67%;
  }
  .signupContainer .cnpass label {
    margin-left: -53%;
  }
  .signupContainer .password label {
    margin-left: -56%;
  }
  .signupContainer .passClass {
    width: 90%;
  }
  .mainIcon {
    display: none;
  }
}
/* ********************************* Footer *********************************************** */

.footer {
  height: 7.5vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(0, 0, 0);
  z-index: 5;
}
.bright .footer {
  box-shadow: inset 1px 1px 36px rgb(108, 99, 255);
}
.dark .footer {
  background-color: #222831;
  color: white;
}
@media (max-width: 480px) {
  .footer {
    /* display: none; */
  }
}

/* *********************************** Home Page *********************************************** */

.mainHomeContainer {
  height: calc(82vh - 15.1vh - 4px + 10px);
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.dark .mainHomeContainer {
  background-color: #31363f;
}
.mainHomeContainer .container {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  padding: 5px;
  /* justify-content: center; */
  width: 30vw;
  height: 50vh;
  box-shadow: 1px 1px 6px rgb(108, 99, 255);
  border-radius: 10px;
}
.mainHomeContainer .container h1 {
  color: rgb(108, 99, 255);
}

.heading h1,
.heading p {
  text-align: center;
}
.dark .heading {
  background-color: #31363f;
  color: white;
}

.mainHomeContainer .container .btn {
  height: 30px;
  width: 50%;
  border-radius: 8px;
  font-family: goldman;
}
.btn button {
  background-color: white;
  border: 1px solid black;
  border-radius: 8px;
  width: 100%;
  height: 100%;
}
.dark .btn button {
  background-color: #222831;
  color: white;
  box-shadow: 1px 1px 4px white;
}
.mainHomeContainer .container h1 {
  margin-bottom: 5px;
  font-family: goldman;
}

.mainHomeContainer .container button:hover {
  cursor: pointer;
  background-color: rgb(108, 99, 255);
  transition: ease-in 0.2s;
  color: white;
}

@media (max-width: 480px) {
  .mainHomeContainer {
    height: auto;
    display: flex;
    flex-direction: column;
    gap: 25px;
  }
  .mainHomeContainer .container:first-child {
    margin-top: 25px;
  }
  .mainHomeContainer .container:last-child {
    margin-bottom: 25px;
  }
  .mainHomeContainer .container {
    width: 70%;
    height: auto;
  }
  .hometitle h1 {
    font-size: 50px;
  }
}

/* ************************************** Coding Container ************************************ */

.mainCodingContainer {
  height: 66.9vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.dark .mainCodingContainer {
  background-color: #31363f;
  color: white;
}
.questionContainer {
  width: 60vw;
  height: 50vh;
  box-shadow: 1px 1px 6px rgb(108, 99, 255);
  border-radius: 10px;
  padding: 15px;
}
.questionContainer p {
  font-weight: bold;
  color: rgb(108, 99, 255);
}
.questionTracker {
  height: 70vh;
  width: 20vw;
  margin-bottom: 20px;
  box-shadow: 2px 2px 6px rgb(108, 99, 255);
  border-radius: 10px;
}
.options {
  margin-top: 10px;
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  gap: 7px;
}
.options label {
  margin-left: 5px;
}
.options input {
  height: 15px;
  width: 15px;
}
.btns {
  width: 60vw;
  align-items: center;
  justify-content: space-around;
  display: flex;
}
.questionContainer button {
  margin-top: 15vh;
  height: 5vh;
  width: 15vw;
  border-radius: 8px;
  border: 1px solid black;
  background-color: white;
  font-weight: bold;
  background-color: rgb(108, 99, 255);
  color: white;
}
.questionContainer button:hover {
  cursor: pointer;
  box-shadow: 2px 2px 6px rgb(108, 99, 255);
  transition: ease-out 0.3s;
}
#submitTest:hover {
  box-shadow: 2px 2px 8px red;
  transition: ease-out 0.3s;
}
.questionTracker {
  padding: 5px;
  display: grid;
  grid-template-columns: auto auto auto;
  height: 45vh;
  width: 30vw;
}
.dark .questionTracker {
  padding: 10px;
  color: black;
}
.questionTracker div {
  box-shadow: 0.5px 0.5px 4px rgb(108, 99, 255);

  border-radius: 5px;
  margin: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: large;
}

.qtboxInformation {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.qtboxInformation div {
  margin-bottom: 10px;
  height: 30px;
  width: 30px;
  box-shadow: 0.5px 0.5px 4px rgb(108, 99, 255);
  border-radius: 5px;
}
.qtboxInformation div:nth-child(1) {
  background-color: rgb(108, 99, 255);
}
.qtboxInformation div:nth-child(3) {
  background-color: green;
}
.qtboxInformation div:nth-child(5) {
  background-color: white;
}
.qtboxInformation strong {
  margin-left: 10px;
  margin-bottom: 10px;
  margin-right: 10px;
}

#solved {
  color: green;
}
#unsolved {
  color: rgb(108, 99, 255);
}
.questionTracker .question.current-question {
  background-color: rgb(108, 99, 255);
}
.codingInformation {
  display: flex;
  width: 100vw;
  align-items: center;
  justify-content: space-around;
}
.codingInformation .score {
  font-weight: bold;
  font-size: large;
  color: red;
}
.codingInformation .time {
  width: 220px;
  font-family: goldman;
  font-size: 0.7rem;
}

.bright .codingInformation .time {
  color: rgb(108, 99, 255);
}
.dark .codingInformation .time {
  color: #222831;
  text-shadow: 1px 1px 4px white;
}
.dark .codingInformation {
  background-color: #31363f;
}
.codingInformation .tracker {
  font-size: large;
  font-weight: bold;
  font-family: goldman;
}
.dark .codingInformation .tracker {
  color: white;
  font-weight: bold;
  font-family: goldman;
}
#submitTest {
  background-color: red;
}
.solved {
  background-color: green;
}
.unsolved {
  background-color: white;
}
.mobileTracker {
  display: none;
}
.mobsub {
  display: none;
}
@media (max-width: 480px) {
  ul li {
    font-size: smaller;
  }

  .mainCodingContainer {
    display: flex;
    flex-direction: column-reverse;
    gap: 25px;
    align-items: center;
    justify-content: center;
    height: auto;
  }
  .questionTrackerContainer {
    width: 100vw;
    display: flex;
    flex-direction: column;
    gap: 25px;
    align-items: center;
    justify-content: center;
  }
  .questionTracker {
    position: relative;
    margin: 0;
    width: 80vw;
  }
  .qtboxInformation {
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .mobileTracker {
    display: flex;
    width: 100vw;
    align-items: center;
    justify-content: center;
    font-family: goldman;
    font-weight: 700;
    font-size: larger;
  }
  .mobileTracker span {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .dark .mobileTracker {
    background-color: #31363f;
    color: white;
  }
  .tracker {
    display: none;
  }
  .questionTracker {
    grid-template-columns: auto auto auto auto auto;
  }

  .questionContainer {
    width: 90vw;
    margin-top: 15px;
    margin-bottom: 50px;
    height: 60vh;
  }
  .dark .questionContainer {
    background-color: #31363f;
  }

  .questionContainer .btns {
    margin-top: -20px;
    width: 100%;
    display: flex;
    align-items: center;
    gap: 20px;
    justify-content: space-between;
  }
  .questionContainer .btns #submitTest {
    display: none;
  }
  .mobsub {
    display: flex;
    width: 100vw;
    align-items: center;
    justify-content: center;
    margin: 0;
  }
  .qtboxInformation {
    margin-top: 25px;
  }
  .mobsub button {
    margin: 15px 0 0 -65px;
    background-color: red !important;
  }

  .mobsub button:hover {
    box-shadow: 0 0 6px red !important;
  }

  .questionContainer button {
    width: 150px;
    height: 35px;
  }
}
/* ************************************** About ****************************************** */

.About {
  height: 82.5vh;
}
.aboutHeading {
  font-size: xx-large;
  font-family: iceberg;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 40%;
}

.aboutHeading h1 span {
  color: rgb(108, 99, 255);
}
.dark .About {
  background-color: #31363f;
  color: white;
}
.dark .heading {
  background-color: #31363f;
}

.subAboutContainer {
  position: relative;
  z-index: 4;
  box-shadow: 1px 1px 4px rgb(108, 99, 255);
  padding: 20px;
  border-radius: 15px;
  display: flex;
  width: 40vw;
  overflow: hidden;
  margin-left: -50px;
}
.subAboutContainer:nth-child(1) {
  width: 44.9vw;
}

.subAboutContainer p {
  overflow: hidden;
  height: 100%;
  font-family: iceberg;
  margin-top: 10px;
  font-size: large;
}
.introduction {
  display: flex;
  justify-content: space-around;
}
.information {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}
.introduction,
.information {
  overflow: hidden;
  height: 100%;
  width: 100%;
}
.introduction .information {
  overflow: hidden;
}
.subAboutContainer img {
  height: 10px;
  filter: drop-shadow(1px 1px 1px blue);
}
.aboutBox {
  display: flex;
  justify-content: space-around;
  gap: 50px;
}

.subAboutBoxes {
  margin-top: 10vh;
}
.introduction h2 {
  font-family: goldman;
}
.mainSubAboutBoxes {
  display: flex;
  height: 70vh;
  /* margin-top: 100px; */
}
.About .mainIcon {
  display: flex;
  z-index: 5;
}
@media (max-width: 480px) {
  .mainSubAboutBoxes {
    margin: 0;
    height: 70vh;
  }
  .subAboutBoxes {
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .subAboutContainer:first-child {
    width: 90%;
  }
  .subAboutContainer {
    margin: 0;
    width: 90%;
  }
  .aboutHeading h1 {
    font-family: iceberg;
    font-size: 50px;
    font-weight: 900;
    text-align: center;
  }

  .aboutHeading h1 span {
    color: rgb(108, 99, 255);
  }
  .mainSubAboutBoxes {
    margin: 0;
  }
}
